#title{
  color: rgb(95, 240, 180);
  text-align: center;
  text-shadow: 1px 2px 1px black;
  font-size: 45px;
  font-family: fantasy, serif, cursive;
  text-align: center;
  /* margin-bottom: 5%; */
  -webkit-box-reflect: below 0px linear-gradient(to left, rgba(0,0,0,0.1), rgba(0,0,0,0.2));
}

#outer-screen{
  position: relative;
  display: flex;
  flex-flow: row;
  top: -6%;
  left: 10%;
  width: 80vw;
  height: 55vh;
  overflow: hidden;
  perspective: 250px;
  -webkit-box-reflect: below 0px linear-gradient(to left, rgba(0,0,0,0.1), rgba(0,0,0,0.5));
}
#frame2{
  transform: translateX(60%);
}
.frame{
  position: relative;
  left: 26%;
  height: 97%;
  width: 50%;
  margin-top: .5%;
  padding: 0;
  box-shadow: .2px .2px 2px rgb(0, 255, 255);
  background-image: linear-gradient(0deg, rgba(0, 255, 255, 0.15), steelblue);
  border-radius: 10px;
  transition-duration: 2s;
  transform: rotateX(-1deg);
  perspective: 550px;
}

.text{
  margin: 3%;
  padding: 2%;
  font-size: 2em;
  color: white;
  font-family: gill sans;
  text-shadow: 1px 1px 0px black;
  word-spacing: 4%;
  border-radius: 10px;
  z-index: -1;
  max-width: max-content;
}

.rotating-board{
  position: absolute;
  top: 50%;
  width: 100%;
  height: 100%;
  background: none;
  transition-duration: 1s;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transform: scaleY(2) rotateX(-90deg);
  animation: play 7s infinite;
}
img{
  position: absolute;
  top: 0;
  width: 100%;
  height: 50%;
  outline: 2.5px solid cyan;
  border-radius: 10px;
  z-index: 1;
}

#author-button-container{
  display: flex;
  flex-flow: row;
  position: relative;
  margin-top: 2%;
}
#author-container{
  position: relative;
  display: flex;
  flex-flow: row;
  position: relative;
  left: 20%;
  width: 30vw;
  height: 7vh;
}
#author{
  position: relative;
  bottom: 92%;
  /* color: skyblue; */
  color: rgb(95, 240, 180);
  text-shadow: 1px 1px 0px black;
  width: max-content;
  height: 55%;
  font-size: 1.4em;
}

span{
  /* border: 1px solid yellow; */
  padding-top: 2%;
  font-size: 1.6em;
  color: steelblue;
  text-shadow: 1px 1px 0px black;
  width: 20%;
}
#next-button{
  position: relative;
  top: 0;
  left: 30%;
  padding: 1%;
  font-size: 1.1rem;
  width: 10%;
  height: 40%;
  border: none;
  color: white;
  text-shadow: 1px 1px 0px black;
  background-image: linear-gradient(0deg, rgba(0, 255, 255, 0.40), steelblue);
  border-radius: 10px;
}

#next-button:hover{
  box-shadow: .1px .1px .5px black;
  transform: scale(1.1);
}