@media only screen and (max-width: 768px){

  #title{
    font-size: 22px;
  }

  #image-screen{
    position: absolute;
    bottom: 15%;
    left: 0%;
    right: 11%;
    width: 93vw;
    height: 49vh;
    visibility: hidden;
  }
  #qoute{
    text-shadow: 1px 1px 0px black;
    font-size: 15px;
  }
  #qoute-screen{
    position: relative;
    
    right: 2px;
    height: 49vh;
    width: 90vw;
  }

  #author-screen{
    position: relative;
    width: 25vw;
    left: 160px;
    top: 10px;
    border: 1px solid black;
  }
  #author{
    font-size: 10px;
    text-shadow: 1px 1px 0px black;
  }

  #next-button{
    position: relative;
    width: 30vw;
    top: 80px;
    left: -30px;
    
  }
 
}